.preloader{position:fixed;width:100vw;height:100vh;background-color:#293133;top:-150%;left:0;transition:top 0.3s;z-index:1000;box-shadow:0 5px 3px 1px black;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.preloader_active{top:0}.preloader_wrapper{position:relative;width:200px;height:200px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-end;align-items:flex-end}.preloader_item{width:30px;background-color:ivory}.preloader_item_1{-webkit-animation:grow 2s ease-out 0.3s infinite;animation:grow 2s ease-out 0.3s infinite;background-color:#9D355C}.preloader_item_2{-webkit-animation:grow 3s ease-out 0.3s infinite;animation:grow 3s ease-out 0.3s infinite;background-color:#FFFEF2}.preloader_item_3{-webkit-animation:grow 2.5s ease-out 0.3s infinite;animation:grow 2.5s ease-out 0.3s infinite;background-color:#FEC290}.preloader_item_4{-webkit-animation:grow 1s ease-out 0.3s infinite;animation:grow 1s ease-out 0.3s infinite;background-color:00B463}.preloader_item_5{-webkit-animation:grow 3.5s ease-out 0.3s infinite;animation:grow 3.5s ease-out 0.3s infinite;background-color:#E52615}@-webkit-keyframes grow{from{height:1%}50%{height:70%}to{height:1%}}@keyframes grow{from{height:1%}50%{height:70%}to{height:1%}}

